import { Component, OnInit } from '@angular/core';
import { StorageService } from '../../services/storage.services';
@Component({
  selector: 'page-calendar',
  templateUrl: 'calendar.html'
})

export class CalendarPage implements OnInit {
  constructor(private storageService: StorageService) { }
  eventSource;
  viewTitle;
  isToday: boolean;
  calendar = {
    mode: 'month',
    currentDate: new Date()
  }; // these are the variable used by the calendar.
  ngOnInit() {
    this.eventSource = this.createRandomEvents();
  }

  onViewTitleChanged(title) {
    this.viewTitle = title;
  }



  createRandomEvents() {
    const events = [];
    let workoutCompleted = JSON.parse(this.storageService.getValue('dateCompleted'));
    workoutCompleted = workoutCompleted === null ? [] : workoutCompleted;
    for (let i = 0; i < workoutCompleted.length; i += 1) {
      const date = new Date(workoutCompleted[i]);
      console.log(date, workoutCompleted[i]);
      const startDay = 1;
      const endDay = 1;
      let startTime;
      let endTime;
      startTime = date;
      endTime = date;
      events.push({
        title: 'Workout Completed',
        startTime: startTime,
        endTime: endTime,
        allDay: false
      });


    }
    return events;
  }

}



