import { UserService } from '../../services/user.services';
import { Component, OnInit } from '@angular/core';
import { NavController } from 'ionic-angular';
import { AngularFireDatabase } from 'angularfire2/database';
import { StorageService } from '../../services/storage.services';
@Component({
  selector: 'page-settings',
  templateUrl: 'settings.html'
})

export class SettingPage implements OnInit {
  WorkOutTime;
  RestTime;
  constructor(private navCtrl: NavController, private userServ: UserService, private storageSer: StorageService, private angulardb: AngularFireDatabase) {
    this.WorkOutTime = this.storageSer.getValue('workOutTime');
    this.RestTime = this.storageSer.getValue('restTime');
    this.WorkOutTime = this.WorkOutTime ? parseInt(this.WorkOutTime, 0) : 35;
    this.RestTime = this.RestTime ? parseInt(this.RestTime, 0) : 10;
  }
  saveBtnClick() {
    this.storageSer.saveValue('workOutTime', this.WorkOutTime);
    this.storageSer.saveValue('restTime', this.RestTime);
    const uid = this.userServ.getUser().uid;
    console.log(uid);
    const settingsDetails = this.angulardb.list('/usersetting', {
      query: {
        orderByChild: 'id',
        equalTo: uid
      }
    });
    settingsDetails.subscribe((data) => {   
      if (data.length === 0 || data === null) {
        settingsDetails.push({ id: uid, workOutTime: this.WorkOutTime, restTime: this.RestTime });
      } else {
        for (let i = 0; i < data.length; i++) {
          settingsDetails.update(data[i].$key, { workOutTime: this.WorkOutTime, restTime: this.RestTime })
        }
      }
    });
    this.navCtrl.pop();
  }
  cancelBtnClick() {
    this.navCtrl.pop();
  }
  ngOnInit() { }
}



