import { LoginPage } from '../login/login';
import { NavController } from 'ionic-angular';
import { Component, OnInit } from '@angular/core';
import { UserService } from '../../services/user.services';
import { AngularFireAuth } from 'angularfire2/auth';
@Component({
  selector: 'page-profile',
  templateUrl: 'profile.html'
})

export class ProfilePage implements OnInit {
  userDetails;
  constructor(private userServ: UserService, private fireAuth: AngularFireAuth,
    private navCtr: NavController) {
    this.userDetails = this.userServ.getUser();
    console.log(this.userDetails);
  }
  logout() {
    this.fireAuth.auth.signOut().then((data) => {
      this.userServ.signOutUser();
      this.userServ.setRootPage(LoginPage);
    }, (error) => { console.log(error); });
  }
  ngOnInit() { }
}



