import { Component, OnInit, AfterViewInit, ViewChild } from '@angular/core';
import { NavController, Navbar } from 'ionic-angular';
import { PosesService } from '../../services/poses.services';
import { StorageService } from '../../services/storage.services';
import { SoundService } from '../../services/sound.services';
@Component({
  selector: 'page-workout',
  templateUrl: 'workout.html'
})

export class WorkOutPage implements AfterViewInit {
  completedWorkout: Number = 0;
  completedTime = 0;
  completedPercentage = 0;
  headerTitle = '';
  totalCount;
  count = 0;
  imagePage = '';
  inter: any;
  WorkOutName: string;
  workoutVisiblity = false;
  restTime = 0;
  restInter;
  poses = [];
  workoutCompleted = false;
  @ViewChild(Navbar) navBar: Navbar;
  constructor(private navController: NavController, private sndServ: SoundService, private posesServ: PosesService, private storageSer: StorageService) {
    let posesSaved = JSON.parse(this.storageSer.getValue('poseslist'));
    posesSaved = posesSaved === null ? [] : posesSaved;
    if (posesSaved.length !== 0) {
      for (let i = 0; i < posesSaved.length; i++) {
        this.poses.push(this.posesServ.Poses[posesSaved[i]]);
      }
    } else {
      this.poses = this.posesServ.Poses;
    }
    this.totalCount = this.poses.length;
    this.completedWorkout = Math.round(this.count / this.totalCount);
  }

  ngAfterViewInit() {
    this.displayWorkOut(this.count);
    this.navBar.backButtonClick = (e: UIEvent) => {
      clearInterval(this.inter);
      clearInterval(this.restInter);
      this.navController.pop();
    };
  }

  displayWorkOut(counter) {

    this.headerTitle = 'Work Out';
    this.workoutVisiblity = true;
    this.imagePage = this.poses[counter].path;
    this.WorkOutName = this.poses[counter].name;
    this.completedTime = 0;
    let workout;
    workout = this.storageSer.getValue('workOutTime');
    workout = (workout) ? parseInt(workout, 0) : 35;
    this.completedPercentage = workout;
    this.inter = setInterval(function () {
      this.completedTime++;
      if (this.completedTime === workout) {
        clearInterval(this.inter);
        this.count++;
        this.completedWorkout = Math.ceil((this.count / this.totalCount) * 100);
        if (document.getElementsByTagName('audio').length === 0) {
          this.sndServ.createSound();
        }
        this.sndServ.playSound();
        this.displayRestTime();
      }
      this.completedPercentage = workout - this.completedTime;

    }.bind(this), 1000);
  }

  displayRestTime() {
    if (this.count < this.totalCount) {
      this.workoutVisiblity = false;
      this.headerTitle = 'Rest Time';
      const restOut = this.storageSer.getValue('restTime');
      this.restTime = (restOut) ? parseInt(restOut, 0) : 10;
      this.restInter = setInterval(function () {
        this.restTime--;
        if (this.restTime === 0) {
          this.skipRestTime();
        }
        if (this.restTime === 1) {
          this.sndServ.playSound();
        }
      }.bind(this), 1000);
    } else {
      let dateCompleted = JSON.parse(this.storageSer.getValue('dateCompleted'));
      dateCompleted = dateCompleted === null ? [] : dateCompleted;
      dateCompleted.push(new Date());
      this.storageSer.saveValue('dateCompleted', JSON.stringify(dateCompleted));
      this.workoutCompleted = true;
    }
  }
  skipRestTime() {
    clearInterval(this.restInter);
    this.displayWorkOut(this.count);
    this.completedTime = 0;
  }
}



