import { Injectable } from '@angular/core';

@Injectable()
export class SoundService {

    audioTag: any;
    constructor() {
        this.audioTag = document.getElementsByTagName('audio')[0];
    }
    createSound() {

    }
    playSound() {
        this.audioTag.load();
        this.audioTag.play();
    }

}



