import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';

import { MyApp } from './app.component';

import { CalendarPage } from '../pages/calendar/calendar';
import { HomePage } from '../pages/home/home';
import { LoginPage } from '../pages/login/login';
import { PosesPage } from '../pages/poses/poses';
import { ProfilePage } from '../pages/profile/profile';
import { RegisterPage } from '../pages/register/register';
import { SettingPage } from '../pages/settings/settings';
import { WorkOutPage } from '../pages/workout/workout';
import { ForgetpasswordPage } from '../pages/forgetpassword/forgetpassword';

import { ProgressComponent } from '../component/progress.component';

import { AngularFireModule } from 'angularfire2';
import { AngularFireDatabaseModule } from 'angularfire2/database';
import { AngularFireAuthModule } from 'angularfire2/auth';
import { NgCalendarModule } from 'ionic2-calendar';

import { UserService } from '../services/user.services';
import { PosesService } from '../services/poses.services';
import { StorageService } from '../services/storage.services';
import { SoundService } from '../services/sound.services';

export const firebaseConfig = {
  apiKey: 'AIzaSyC1icmVHMTAGobhnZeNzAXrR59F82es-fc',
  authDomain: 'brpexpensetracker.firebaseapp.com',
  databaseURL: 'https://brpexpensetracker.firebaseio.com',
  projectId: 'brpexpensetracker',
  storageBucket: 'brpexpensetracker.appspot.com',
  messagingSenderId: '464174354421'
};

@NgModule({
  declarations: [
    MyApp,
    CalendarPage,
    ForgetpasswordPage,
    HomePage,
    LoginPage,
    PosesPage,
    ProfilePage,
    RegisterPage,
    SettingPage,
    WorkOutPage,
    ProgressComponent
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp),
    AngularFireModule.initializeApp(firebaseConfig),
    AngularFireAuthModule,
    AngularFireDatabaseModule,
    NgCalendarModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    CalendarPage,
    ForgetpasswordPage,
    LoginPage,
    HomePage,
    PosesPage,
    ProfilePage,
    RegisterPage,
    SettingPage,
    WorkOutPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    UserService,
    PosesService,
    StorageService,
    SoundService,
    { provide: ErrorHandler, useClass: IonicErrorHandler }
  ]
})
export class AppModule { }



