
import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { AngularFireAuth } from 'angularfire2/auth';
import { AlertController } from 'ionic-angular';
/**
 * Generated class for the RegisterPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */

@Component({
  selector: 'page-register',
  templateUrl: 'register.html',
})
export class RegisterPage {
  name;
  userName;
  password;
  constructor(private alertCtrl: AlertController, public navCtrl: NavController, public navParams: NavParams, private af: AngularFireAuth) {
  }

  ionViewDidLoad() {

  }
  registerClick() {
    if (this.name && this.userName && this.password) {
      this.af.auth.createUserWithEmailAndPassword(this.userName, this.password).then((data) => {
        this.af.auth.currentUser.sendEmailVerification();
        this.navCtrl.pop();
      });
    } else {
      const alert = this.alertCtrl.create({
        title: 'Error',
        subTitle: 'Please fill all the fields',
        buttons: ['Ok']
      });
      alert.present();
    }
  }
}



