import { Component } from '@angular/core';
import { PosesService } from '../../services/poses.services';
import { StorageService } from '../../services/storage.services';

@Component({
  selector: 'page-poses',
  templateUrl: 'poses.html'
})
export class PosesPage {
  poses = [];
  constructor(private posesSer: PosesService, private strServ: StorageService) {
    this.poses = posesSer.Poses;
    let posesList = JSON.parse(this.strServ.getValue('poseslist'));
    posesList = (posesList === null) ? [] : posesList;
    posesList.sort(function (a, b) {
      return a - b;
    });

    for (let i = 0; i < this.poses.length; i++) {
      if (posesList.length === 0 || posesList.indexOf(i) > -1) {
        this.poses[i].checked = true;
      } else {
        this.poses[i].checked = false;
      }
    }
    console.log(this.poses);
  }
  updateValues(item) {
    const posesList = [];
    for (let i = 0; i < this.poses.length; i++) {
      if (this.poses[i].checked === true) {
        posesList.push(i);
      }
    }
    this.strServ.saveValue('poseslist', JSON.stringify(posesList));
  }
}



