import { StorageService } from '../../services/storage.services';
import { AfterContentInit, Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { AngularFireAuth } from 'angularfire2/auth';
import { AngularFireDatabase } from 'angularfire2/database';
import * as firebase from 'firebase/app';
import { HomePage } from '../home/home';
import { RegisterPage } from '../register/register';
import { ForgetpasswordPage } from '../forgetpassword/forgetpassword';
import { UserService } from '../../services/user.services';
import { AlertController } from 'ionic-angular';
@Component({
  selector: 'page-login',
  templateUrl: 'login.html'
})

export class LoginPage implements AfterContentInit {
  user;
  socialNW = false;
  userName; password;
  constructor(private alertCtrl: AlertController, public navCtrl: NavController, private fireAuth: AngularFireAuth,
    private db: AngularFireDatabase, private userSer: UserService, private storageSer: StorageService) {

  }
  ngAfterContentInit() {
    this.user = this.fireAuth.authState;
    this.user.subscribe((d) => {
      if (d !== null) {
        this.userSer.setUser(d);
        const loginDetails = this.db.list('/user', {
          query: {
            orderByChild: 'id',
            equalTo: d.uid
          }
        });
        loginDetails.subscribe((data) => {
          if (data.length === 0 || data === null) {
            loginDetails.push({ displayName: d.displayName, id: d.uid, email: d.email });
          }
        }, (error) => {
          if (!this.userSer.getSignOut()) {
            this.displayAlert('error', error);
          }
        });
        if (d !== null) {
          if (d.emailVerified) {
            this.userSer.setRootPage(HomePage);
            const settingsDetails = this.db.list('/usersetting', {
              query: {
                orderByChild: 'id',
                equalTo: d.uid
              }
            });
            settingsDetails.subscribe((data) => {
              console.log(data);
              if (data.length !== 0) {
                this.storageSer.saveValue('workOutTime', data[0].workOutTime);
                this.storageSer.saveValue('restTime', data[0].restTime);
              }
            }, (error) => {
              if (!this.userSer.getSignOut()) {
                this.displayAlert('error', error);
              }
            });
          } else {
            const alert = this.alertCtrl.create({
              title: 'Email Verify',
              subTitle: 'Please Check your mail to verify your email address',
              buttons: [
                {
                  text: 'Ok',
                  role: 'cancel',
                  handler: () => {
                    console.log('Cancel clicked');
                  }
                },
                {
                  text: 'Send Email Again',
                  handler: () => {
                    this.fireAuth.auth.currentUser.sendEmailVerification();
                  }
                }
              ]
            });
            alert.present();
          }
        }
      }
    });
  }
  signIn() {
    this.fireAuth.auth.signInWithEmailAndPassword(this.userName, this.password);
  }
  register() {
    this.navCtrl.push(RegisterPage);
  }
  frgtPwd() {
    this.navCtrl.push(ForgetpasswordPage);
  }
  gmailLogin() {
    this.fireAuth.auth.signInWithPopup(new firebase.auth.GoogleAuthProvider());
  }
  linkedInLogin() {
    this.fireAuth.auth.signInWithPopup(new firebase.auth.FacebookAuthProvider());
  }
  facebookLogin() {
    this.fireAuth.auth.signInWithPopup(new firebase.auth.FacebookAuthProvider());
  }
  githubLogin() {
    this.fireAuth.auth.signInWithPopup(new firebase.auth.GithubAuthProvider());
  }
  displayAlert(title, subtitle) {
    const alert = this.alertCtrl.create({
      title: title,
      subTitle: subtitle,
      buttons: [
        {
          text: 'Ok',
          role: 'cancel',
          handler: () => {
            console.log('Cancel clicked');
          }
        }
      ]
    });
    alert.present();
  }
}



